* {
  font-family: sans-serif;
}
.page {
  padding: 10px;
  position: relative;
  margin-left: 20px;
  margin-right: 20px;
}
div {
  margin: 5px;
}

.pageHeader {
  text-align: center;
  margin-top: 0;
  margin-bottom: 10px;
}

.productGrid {
  width: 50%;
  float: left;
}
.cartGrid {
  width: 40%;
  float: left;
  margin-top: 60px;
}
table {
  border: 1px solid black;
}

.productTbl {
  margin-top: 50px;
}

td {
  border: 1px solid red;
}

.tdProduct {
  position: relative;
  width: 150px;
  height: 130px;
  text-align: center;
  vertical-align: top;
  font-size: 15px;
  padding-top: 10px;
}

.tdProduct .price {
  position: absolute;
  bottom: 40px;
  left: 25%;
}
.addBtn {
  position: absolute;
  bottom: 10px;
}

.btnDisabled {
  color: red;
  right: 18%;
}
.btnEnabled {
  color: black;
  right: 25%;
}
.cart {
  right: 25%;
  position: absolute;
}

.filterGrid {
  position: relative;
  left: 18px;
}

.filterGrid div {
  position: absolute;
  right: 64%;
  top: 15%;
  bottom: 20px;
}
.filterGrid span {
  font-size: 15px;
}
.filterGrid select {
  font-family: sans-serif;
  height: 20px;
}

.cartGrid li {
  border-bottom: 1px solid black;
  margin-bottom: 10px;
  float: left;
  width: 100%;
}
.cartGrid li span {
  margin-right: 5px;
}

.cartGrid button {
  border: 2px solid red;
  border-radius: 20px;
  margin-left: 10px;
  font-weight: bold;
}

.cartGrid .prodDetails {
  float: left;
  width: 70%;
  margin-top: 0;
  margin-bottom: 10px;
}

.cartGrid .prodName {
  width: 230px;
  display: inline-block;
}
.cartGrid .prodPrice {
  text-align: right;
  float: left;
  width: 15%;
  font-weight: bold;
  margin-top: 0;
}

.cartGrid .totalPrice {
  float: right;
  margin-right: 11%;
  font-weight: bold;
}
.cartGrid .qty {
  line-height: normal;
}

.cartGrid .qtyDetails {
  margin-top: 0;
  vertical-align: top;
  display: inline-block;
}
.borderNone {
  border: none;
}
.border {
  border: 1px solid red;
}
